.btn {
  background-image: var(--gradient);
  box-shadow: 3.5px 6.062px 16px 0px rgba(2, 2, 2, 0.41);
  color: inherit;
  font-weight: 600;
  line-height: 100%;
  text-transform: uppercase;
  border-radius: var(--border-radius);
  border: var(--dotted-border);
  padding-bottom: 3px;
  cursor: pointer;
  transition: background 1s linear;
}

.btn-lg {
  min-width: 190px;
  height: 100px;
  font-size: 3rem;
  padding: 0 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn-sm {
  min-width: 100px;
  height: 45px;
  font-size: 1.75rem;
  padding: 0 15px 2px 15px;
}

.btn-round {
  position: relative;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  font-size: 1.4rem;
  z-index: 101;
  animation: pulse 300ms linear 250ms 3 forwards;
  /* animation: name duration timing-function delay iteration-count direction fill-mode; */
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.btn:hover {
  background-size: 200%;
  background-position: right center;
}

@media screen and (max-width: 600px) {
  .btn-lg {
    min-width: 85px;
    max-width: 85px;
    height: 85px;
    font-size: 2rem;
    padding: 0 15px;
    text-align: center;
  }

  .btn-sm {
    height: 40px;
    font-size: 1.25rem;
    padding: 0 15px 2px 15px;
  }

  .play-btn {
    min-width: 190px;
  }
}
